<template>
    <div>
        <server-info v-if="currentStep === steps.findIndex(step=>step.key === 'serverInfo')"/>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process :type="1" v-if="currentStep === steps.findIndex(step=>step.key === 'process')" :handle-business="handleBusiness"/>
        <input-car v-if="currentStep === steps.findIndex(step=>step.key === 'choiceCar')" :nextFunction="inputCarInfo"/>
        <text-appear v-if="currentStep === steps.findIndex(step=>step.key === 'carInfo')" title="机动车信息确认" :formListData="exemptionCarInfo" :media="mediaCarInfo"></text-appear>
        <input-time v-if="currentStep === steps.findIndex(step=>step.key === 'inputTime')" :nextFunction="inputTime"/>
        <high-photo
            v-if="currentStep === steps.findIndex(step=>step.key === 'highPhoto')"
            itemName="交强险"
            :nextFunction="highPhotoNext"
        />
        <mail-type v-if="currentStep === steps.findIndex(step=>step.key === 'mailType')" :nextFunction="mailAddressUpload" :placeOfCollectionArr="placeOfCollectionArr" :mailTip="mailTip"/>
        <process
            :type="2"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process2')"
            :handle-business="handleBusinessComplete"
        />
        <process :type="3" v-if="currentStep === steps.findIndex(step=>step.key === 'tickPrint')" :handle-business="tickPrint" waitText="业务凭条打印中，请稍后......"/>
        <complete v-if="currentStep === steps.findIndex(step=>step.key === 'complete')" :data="exemptionCompleteInfo" footTexts="温馨提示：请取走业务申请表或凭条"/>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import process from '@/components/process';
import serverInfo from '@/components/server-info';
import complete from '@/components/complete';
import inputCar from '@/view/pageComponents/input-car';
import textAppear from '@/view/pageComponents/text-appear';
import inputTime from '@/view/pageComponents/input-time';
import highPhoto from '@/view/pageComponents/high-photo';
import mailType from '@/view/pageComponents/mail-type';
import { SIX_YEAR_EXEMPTION } from '@/constant/stepsOption';
import { getCarInfo, examptionFaceRecognition, examptionMaterialPhoto, uploadMaterialInfo, getExamptionCompleteInfoNew, carInsuranceInfo } from '@/service/traffic/vehicle/examption';
import mediaCarInfo from '@/assets/media/trafic/15.mp3';
import { cancelPrefix } from '@/utils/tools';
// import { tickPrint } from '@/service/HardwareRequest';
// import { getCarTick } from '@/constant/tickertape';
import { vehLicenseSaveMailInfo } from '@/service/traffic/vehicle/licenseRenewal';
import { excelPrint } from '@/service/common';
import request from '@/utils/request';

export default {
    name: 'six-year-exemption-bb', // 六年免检
    components: { identifyCardPage, faceYitu, contrast, serverInfo, complete, process, inputCar, textAppear, inputTime, highPhoto, mailType },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            identifyCard: 'identifyCard',
            binocularCamera: 'binocularCamera'
        })
    },
    data () {
        return {
            mailTip: '提示：选择邮寄方式，证件将在两个工作日内寄出，邮寄费用以到付方式收取，标准参考如下，以实际付费为准: 蚌埠一市三县20元（车牌25元），省内25元（车牌30元），跨省30元（车牌30元）',
            placeOfCollectionArr: [
                { type: '30', value: '邮寄' }
            ],
            exemptionCarInfo: {},
            mediaCarInfo: mediaCarInfo,
            exemptionCompleteInfo: {},
            hphm: '',
            hpzl: ''
        };
    },
    mounted () {
        this.setSteps(SIX_YEAR_EXEMPTION);
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        async handleBusiness () {
            const res = await examptionFaceRecognition({
                name: this.identifyCard.partyName,
                identifiedPhoto: this.binocularCamera
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async inputCarInfo ({ carLicense, carType }) {
            this.hphm = carLicense;
            this.hpzl = carType;
            const res = await getCarInfo({
                idNum: this.identifyCard.certNumber,
                plateType: carType,
                plateNumber: carLicense
            });
            if (res && res.code === '0') {
                this.exemptionCarInfo = res.result;
                await this.checkMatch();
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async checkMatch () {
            const res = await carInsuranceInfo({
                plateType: this.hpzl,
                plateNumber: this.hphm,
                idNum: this.identifyCard.certNumber
            });
            // 获取 机动车交强险信息
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async inputTime ({ startTime, endTime }) {
            const res = await uploadMaterialInfo({
                compulsoryInsuranceBeg: startTime,
                compulsoryInsuranceEnd: endTime
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async highPhotoNext (photo) {
            const res = await examptionMaterialPhoto({
                insurancePhoto: cancelPrefix(photo)
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async mailAddressUpload (mailInfo) {
            const res = await vehLicenseSaveMailInfo({
                idNum: this.identifyCard.certNumber,
                ...mailInfo
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async handleBusinessComplete () {
            const res = await getExamptionCompleteInfoNew({});
            if (res.code === '0') {
                this.exemptionCompleteInfo = res.result;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async checkPayStatus () {

        },
        async tickPrint () {
            // exemptionCompleteInfo
            const res = await excelPrint({
                path: request.getServerUrl() + this.exemptionCompleteInfo.paperPath
            });
            if (res && res.code === 1) {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: '打印机出故障啦，请稍后重试' });
            }
            // const data = {
            //     businessTypeName: this.businessTypeName,
            //     hphm: this.hphm,
            //     hpzl: this.hpzl,
            //     certNumber: this.identifyCard.certNumber,
            //     partyName: this.identifyCard.partyName,
            //     clsbdh: this.carInfo.clsbdh,
            //     lsh: this.exemptionCompleteInfo.lsh,
            //     jbr: this.exemptionCompleteInfo.jbr
            // };
            // const res = await tickPrint({ content: getCarTick(data) });
            // this.changeStep('down');
            // console.log('打印凭条结果：', res);
        }
        // async photoNext (chosePhoto) {
        //     const res = await handlePhotoRequest(chosePhoto, this.CONSTANT);
        //     if (res && res.code === 1) {
        //         this.handlePic = res.obj;
        //         this.changeStep('down');
        //     } else {
        //         this.changeTipPage({ message: res.message, enableBack: true });
        //     }
        // },
        // async uploadPhoto () {
        //     const res = await uploadPicFtp({
        //         photoStr: cancelPrefix(this.handlePic)
        //     });
        //     if (res && res.code === '0') {
        //         if (this.noFinger) {
        //             this.changeStep('process');
        //         } else {
        //             this.changeStep('down');
        //         }
        //    } else if (res) {
        //         this.changeTipPage({ message: res.message, enableBack: true });
        //     }
        // },
    }
};
</script>
