<template>
    <div>
        <div class="kd-page-title">填写交强险保单信息</div>
        <div class="kd-page-content-wrapper kd-page-padding wrap">
            <div class="item">
                <p class="label">生效日期</p>
                <el-date-picker
                    ref="startRef"
                    value-format="yyyy-MM-dd"
                    class="timeWrap"
                    v-model="startTime"
                    type="date"
                    placeholder="选择日期"
                    size="large"
                    align="left"
                    popper-class="compulsoryTime"
                    :picker-options="pickerStartOptions"
                />
            </div>
            <div class="item">
                <p class="label">终止日期</p>
                <el-date-picker
                    ref="endRef"
                    value-format="yyyy-MM-dd"
                    class="timeWrap"
                    v-model="endTime"
                    type="date"
                    placeholder="选择日期"
                    size="large"
                    align="right"
                    popper-class="compulsoryTime"
                    :picker-options="pickerEndOptions"
                />
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
            <kd-button icon="kedaIcon-circleright" text="下一步" :button-handle="nextStep" :but-disable="!(startTime && endTime)"/>
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
// import { playAudio } from '@/utils/media';

export default {
    name: 'input-time',
    props: {
        nextFunction: {
            type: Function,
            default: function () {
                this.changeStep('down');
            }
        }
    },
    data () {
        return {
            startTime: '',
            endTime: '',
            pickerStartOptions: {
                disabledDate (time) {
                    return time.getTime() > Date.now();
                }
            },
            pickerEndOptions: {
                disabledDate (time) {
                    return time.getTime() <= Date.now();
                }
            }
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        async nextStep () {
            await this.nextFunction({ startTime: this.startTime, endTime: this.endTime });
        }

    },
    mounted () {
        this.$refs.startRef.focus();
        // this.$refs.endRef.focus();
        // this.audioCar = playAudio(audioCar);
        // this.audioCar.play();
    },
    beforeDestroy () {
        // this.audioCar.destroy();
    }
};
</script>

<style>
    .compulsoryTime {
        width: 50vw;
    }

    .compulsoryTime .el-picker-panel__content {
        width: auto;
    }

    .compulsoryTime .el-date-table {
        font-size: 24px;
    }

    .compulsoryTime .el-date-table td {
        padding: 1PX;
    }

    .compulsoryTime .el-date-table td span {
        width: 35px;
        height: 35px;
        line-height: 35px;
    }

    .compulsoryTime .el-date-table td div {
        height: 35px;
        box-sizing: border-box;
        padding: 0;
    }

    .compulsoryTime .el-date-picker__header-label {
        font-size: 22px;
    }

    .compulsoryTime .el-picker-panel__icon-btn {
        font-size: 28px;
        margin: 0;
    }

    .compulsoryTime .el-year-table td {
        padding: 1PX;
    }

    .compulsoryTime .el-year-table td.today .cell {
        width: auto;
        height: auto;
        line-height: 35px;
        font-size: 24px;
    }

    .compulsoryTime .el-year-table td .cell {
        width: auto;
        height: auto;
        line-height: 35px;
        font-size: 24px;
    }

    .compulsoryTime .el-month-table td .cell {
        width: auto;
        height: auto;
        line-height: 35px;
        font-size: 24px;
        border-radius: 0;
    }

</style>

<style lang="scss" scoped>
    .wrap {
        display: flex;
        justify-content: space-around;

        .item {
            /*margin-top: 60px;*/
        }

        .label {
            margin-bottom: 20px;
            font-size: 25.6px;
        }

        .timeWrap {
            height: 60px;
            width: 350px;

            ::v-deep .el-input__inner {
                height: 60px;
                font-size: 22.4px;
                padding-left: 40px;
            }

            ::v-deep .el-input__icon {
                font-size: 19.2px;
            }
        }

        ::v-deep .el-date-picker {
            width: 50%;
        }
    }
</style>
